/*
 * Main layouts for our pages
 *
 *
 * $this includes:
 * header Layout
 * Navigation Layout
 * Grids
 * Footer
 *
 */



 /* ==========================================================================
   Layout Styles: 
   ========================================================================== */

.mt-layout{
  width: 100%;
  height: 100%;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
      -ms-flex-direction: column;
          flex-direction: column;
  position: relative;
  -webkit-overflow-scrolling: touch; 
}

.mt-layout-container{
    position: absolute;
    width: 100%;
    height: 100%;
}

 /* ==========================================================================
   Header layout Styles: 
   ========================================================================== */

.mt-layout-header>* {
    -webkit-flex-shrink: 0;
    -ms-flex-negative: 0;
    flex-shrink: 0;
}

.just-main{
    min-height: 48px;
    max-height: 48px;
    .mt-sub-header{
        visibility: collapse;
    }
}

.mt-glow-soft {
    box-shadow: 0px 0px 1px 1px rgba(0, 0, 0, 0.14), 0px 0px 2px 2px rgba(0, 0, 0, 0.098), 0px 0px 5px 1px rgba(0, 0, 0, 0.084);
}


.mt-global-header{
    min-height: 48px;
    max-height: 48px;
}

.flex-center{
  display: flex;
    justify-content: center;
    align-items: center;
}
.pull-left{
    margin: 0;
    height: 45px;

}

.mdl-layout__header{
  min-height: 45px;
}

.mdl-layout__header.is-compact {
    max-height: 45px;
}

.pull-left>a{
      padding: 1em 0.5em 1em 0;
}

.mt-sub-header{
    justify-content: center;
    background: #Fff;
    height: 64px;
    overflow: visible;
    overflow-y: hidden;
    padding: 0 40px 0 40px;
}

.mt-top-menu>ul.user-actions{
    padding: 0;
    >li{
          margin: 0 14px 0 10px;
    }
}

ul.user-actions>li>a{
    transition: color linear 2ms;
    margin-bottom: 9px;
    &:hover{color: #d3d1d1;}         
  }

.mt-top-avatar{
    max-width: 35px;
    border-radius: 27px;
    display: inline-block;
    overflow: hidden;
    width: 33px;
    height: 33px;
}

header.mdl-layout__header--waterfall:not(.is-compact){

    overflow: visible;
}

header.mdl-layout__header--waterfall.is-compact ul.mt-secondary__navigation{
    display: none !important;
}
 /* ==========================================================================
  SideBar layout Styles: 
   ========================================================================== */

.mt-layout-sidebar{
    @extend .make-flex !optional;
    -webkit-transform: translateX(-18.45rem);
    -ms-transform: translateX(-18.45rem);
    transform: translateX(-18.45rem);
    width: 18.45rem;
    height: 100%;
    max-height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    box-sizing: border-box;
    background: #fafafa;
    color: #424242;
    overflow: visible;
    overflow-y: auto;
    z-index: 5;
    -webkit-transform-style: preserve-3d;
    transform-style: preserve-3d;
    will-change: transform;
    -webkit-transition-duration: .4s;
    transition-duration: .4s;
    -webkit-transition-timing-function: cubic-bezier(.37,.15,.32,.94);
    transition-timing-function: cubic-bezier(.37,.15,.32,.94);
    -webkit-transition-property: -webkit-transform;
    transition-property: transform;
        -webkit-box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.75);
    -moz-box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.75);
    box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.75);
}

header.mt-layout-sidebar__users-info-wrapp{
        box-sizing: border-box;
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -webkit-flex-direction: column;
    -ms-flex-direction: column;
    flex-direction: column;
    -webkit-box-pack: start;
    -webkit-justify-content: flex-start;
    -ms-flex-pack: start;
    justify-content: flex-start;
    padding: 16px;
    box-shadow: 0 2px 2px 0 rgba(148, 148, 148, 0.14), 0 3px 1px -2px rgba(132, 132, 132, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);

}
.mt-sidebar-avatar>img{
    width: 70px;
    height: 70px;
    border-radius: 43px;
    display: inline-block;
    overflow: hidden;
}

.sidebar-title-wrapper{
    margin-top: 1em;
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    position: relative;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -webkit-flex-direction: row;
    -ms-flex-direction: row;
    flex-direction: row;
    -webkit-box-align: center;
    -webkit-align-items: center;
    -ms-flex-align: center;
    align-items: center;
    width: 100%;
     span{text-transform: Capitalize;}
}


.mt-nav ul li{
    margin: 0;
    display: block;
    box-sizing: border-box;
    list-style: none !important;
}

.mt-nav .mt-sidebar-list .mt-sidebar-link{
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -webkit-flex-direction: row;
    -ms-flex-direction: row;
    flex-direction: row;
    text-transform: capitalize;
    -webkit-box-align: center;
    -webkit-align-items: center;
    -ms-flex-align: center;
    align-items: center;
    text-align: left;
    letter-spacing: 0;
    font-size: 15px;
    display: -webkit-box !important;
    display: -webkit-flex !important;
    display: -ms-flexbox !important;
    display: flex !important;

    &:active{
        -webkit-box-shadow: none;
        box-shadow: none;
    }
    i{
      font-size: 30px;
      margin-right: 18px;
    }
}

a.mt-sidebar-link{
    font-weight: 600;
    padding: 1em 1.5em;
    color:rgba(10, 10, 10, 0.68);
    margin: 0;
        &:hover{
              background:rgba(148, 159, 164, 0.13) !important;
    }
}


.mt-list-active>a{
      background:rgba(148, 159, 164, 0.13) !important;
      color: map-get($ui-color, redish);


}

 /* ==========================================================================
  Page layout & content: 
   ========================================================================== */

.mt-page-wrapper{
    z-index: 1;
    top: 45px;
    position: relative;
}

.mt-layout-content{
  min-height: 44em;
}


 /* ==========================================================================
  BreadCrumps: 
   ==========================================================================*/

.dashboard-main-header{
    background: #Fff;
    padding: 1em 0;
}

.breadcrump{   
    padding: 8px 15px;
    margin: 7px 0;
    list-style: none;
    background-color: inherit;
    white-space: nowrap;
}

.breadcrump>li{
    display: inline-block;
     margin: 0;
    text-transform: capitalize;
}

.breadcrump>li+li:before {
    content: "/ ";
    padding: 0 5px;
    color:rgb(66, 65, 65);
}

.breadcrump>li>a{    
    color: map-get($ui-color, link);
    font-size: 98%;

}


/* ==========================================================================
  DropDown: 
   ==========================================================================*/
.mt-menu-container{
    background: #fff;
    padding: 0.3em 0;
    position: absolute;
    right: 10px;
    top: 43px;
    z-index: 999;
    box-shadow: 0 2px 2px 0 rgba(0,0,0,.14),0 3px 1px -2px rgba(0,0,0,.2),0 1px 5px 0 rgba(0,0,0,.12);
    transition: all 0.3s ease;
    transform: scale(0);
    webkit-transform-origin: 100% 0;
    -ms-transform-origin: 100% 0;
    transform-origin: 100% 0;
    opacity: 0;
}

.mt-menu-container>ul>li{
    display: block;
    padding: 0;
    margin: 0;

}

.mt-menu-container>ul>li>a{
    padding: 0.9em 2em;
    display: block;
    font-size: 14px !important;

    &:hover{
            background: rgba(148, 159, 164, 0.13) !important;}
}


.showMenu{
    -webkit-transform: scale(1);
    -ms-transform: scale(1);
    transform: scale(1) !important;
    opacity: 1;
}

.mt-menu__link{
  margin-left: -15px;
  margin-right: -16px;
  padding: 0 24px;
  display: block;
}

/*No result*/

.mt-noresult{
      text-align: center;
    color: #5f5f5f;
    padding: 1em 0;
    margin: 0 auto;
    border-radius: 4px;
    width: 500px;
    background: #fff;
    cursor: pointer;
}

 /* ==========================================================================
  Footer Layout: 
   ========================================================================== */
   
  .mt-layout-footer{
    color: rgb(158, 158, 158);
    background-color: map-get($ui-color, darkbrown);
    padding: 16px 40px;
    min-height: 10em;
    position: relative;
  }

  .mt-obfuscator{
    z-index: 3;
    background-color: transparent;
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    visibility: hidden;
    -webkit-transition-property: background-color;
    transition-property: background-color;
    -webkit-transition-duration: .2s;
    transition-duration: .2s;
    -webkit-transition-timing-function: cubic-bezier(.4,0,.2,1);
    transition-timing-function: cubic-bezier(.4,0,.2,1);

    &.show{
          background-color: rgba(0,0,0,.5);
          visibility: visible;
    }
  }


.mdl-menu__item>a{
      width: 100%;
    display: block;
}
.mt-layout__90{
    width: 90%;
    margin: 0 auto;
}

.mt-top-menu{
    padding: 5px 5px;
    position: absolute;
    top: 0;
    height: 45px;
    right: 0;
}

.profile--wrapper{
  margin-top: 16px; 
}